import React from 'react'
import PropTypes from 'prop-types'

const propTypes = {
  size: PropTypes.string
}

const defaultTypes = {
  size: 'medium'
}

class Button extends React.Component {
  static SIZES: {
    large: 'btn-lg',
    medium: '',
    small: 'btn-sm',
    xsmall: 'btn-xs',
  }

  render() {

    return (
      <button>
        {this.props.children}
      </button>
    )
  }
}

Button.propTypes = propTypes

export default Button



